export async function generateQR(stringData) {
  try {
    qrBase64 = await qrCode.toDataURL(stringData, {
      errorCorrectionLevel: "H",
      type: "image/jpeg",
      quality: 0.3,
      margin: 1,
      color: {
        dark: "#000000",
        light: "#ffffff",
      },
    });
    return qrBase64;
  } catch (error) {
    console.log(error);
  }
}

